@import url('https://fonts.googleapis.com/css2?family=DynaPuff&display=swap');
.game {
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* background-image: url(${myBackgroundImage});
        background-size: cover; */
    font-family: 'DynaPuff', cursive;
}

.game {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.board-row {
    display: flex;
    flex-direction: row;
}

.square {
    background-color: #fff;
    border: 1px solid #999;
    font-size: 48px;
    font-weight: bold;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 180px;
    height: 180px;
    transition: all .2s;
}

.square:hover {
    background-color: #e6e1e1;
}

.square:active {
    background-color: #ffffff;
}

.square:focus {
    outline: none;
}

.reset {
    padding: 10px;
    font-size: 28px;
    font-weight: bold;
    border-radius: 5px;
    background-color: rgb(164, 231, 151);
    color: black;
    min-width: 180px;
    height: 60px;
    border: none;
    transition: all .2s;
}

.reset:hover {
    background-color: rgb(58, 211, 27);
}

.reset:active {
    background-color: rgb(255, 255, 255);
}

.background-pic {
    z-index: -999;
    position: absolute;
    width: 100%;
    height: 100%;
}

.status {
    margin-top: 20px;
    color: white;
    min-width: 480px;
    height: 90px;
    font-size: 60px;
    font-weight: bold;
    text-align: center;
}